@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Press Start 2P', cursive;
}

.container {
    height: 100vh;
    width: 100%;
    display: flex;
    background-color: #edf0f5;
    align-items: center;
    justify-content: center;
}

.text , .form {
    height: 100vh;
}

.text {
    flex: .4 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 0 40px;
}

.text h1 {
    color: #1877f2;
    font-size: 3rem;
    margin-right: 150px;
}

.text p {
    width: 90%;
}

.form {
    flex: .4 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.form form {
    width: 396px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
    padding: 30px 0 38px;
    border-radius: 8px;
    background-color: #fff;

}

.forgot__password {
    font-size: .7rem;
    color: #1877f2;
    border-bottom: 1px solid lightgray;
    padding-bottom: 5px;
}

.signup__modal {
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    height: 520px;
    width: 450px;
    overflow: hidden;
}

.signup__modal .title p {
    font-size: 1.5rem;
    border-bottom: 1px solid lightgray;
    width: 100%;
    padding-bottom: 5px;
}

.signup__modal .title p span {
    font-size: 10px;
    color: #606770;
}

.signup__modal .inputs {
    width: 100%;
    margin-top: 10px;
}

.signup__modal .inputs .names {
    display: flex;
    justify-content: space-around;
    margin-bottom: 10px;
}

.signup__modal .inputs .names input {
    font-size: .8rem;
    margin: 0 5px;
}

.signup__modal .inputs .email, .signup__modal .inputs .password {
    margin-bottom: 10px;
}

.signup__modal .inputs .gender p{
    font-size: .6rem;
    color: #606770;
}

.signup__modal .inputs .terms {
    color: #777;
    font-size: .6rem;
    margin-bottom: 10px;
}

.signup__modal .inputs .signupBtn {
    display: flex;
    align-items: center;
    justify-content: center;

}

.signup__modal .crossBtn {
    position: absolute;
    right: 5%;
    top: 3%;
    color: gray;
}
